<template lang="pug">
.makeresume
    .topBar 機器人
    .chabotHead(:class='chaImage')
    .sayAndTest
        .robotSay.charobotSay {{name}}您好，我是您的機器人傑瑞。 我會在您的聊天室中擔任您的小幫手，趕快去發起計畫或是加入感興趣的計畫吧！
    .chaAccBtn(@click="changePage()") 接受
</template>

<script>

export default {
    components: {
        
    },
    mounted(){
        if(this.$route.query.user)
            this.name = this.$route.query.user;
        if(this.$route.query.charitristic){
            this.charitristic = this.$route.query.charitristic;
            if(this.charitristic=='老虎'){
                this.chaImage = 'tiger';
            }
            if(this.charitristic=='孔雀'){
                this.chaImage = 'peacock';
            }
            if(this.charitristic=='貓頭鷹'){
                this.chaImage = 'owl';
            }
        }
    },
    data() {
        return{
            name: '小花',
            charitristic: '無尾熊',
            chaImage: 'koala'
        }
    },
    methods: {
        changePage: function(){
            if(this.$route.query.user){
                this.$router.push('/?user='+this.$route.query.user);
            }
            else{
                this.$router.push('/');
            }
        }
    }
};
</script>

