<template lang="pug">
.SharePage
    .outcomeIntro
        .articleImg(:style="{'background-image': 'url(' + img + ')'}")
        .articleText
            .articleTitle {{title}}
            .articleDate {{date}}
            .PageText
                div 計畫內容：
                div {{intro}}
    .outcomeProcess
        .PageTitle 執行過程
        .processImg
            .processPic.processPic1(:style="{'background-image': 'url(' + processpic1 + ')'}")
            .processPic.processPic2(:style="{'background-image': 'url(' + processpic2 + ')'}")
            .processPic.processPic3(:style="{'background-image': 'url(' + processpic3 + ')'}")
        .PageText {{processtext}}
    .outcomeSTAR
        .PageTitle S.T.A.R 結果分析
        .PageSubtitle 情況及任務（Situation/Task）：被相關單位駁回 
        .PageText {{st}}
        .PageSubtitle 事後檢討（Analyze）：初期規劃要有遠見
        .PageText {{a}}
        .PageSubtitle 事後補救（Result）：變換場域，重新出發
        .PageText {{r}}
    .outcomeMember
        .PageTitle 成員
        .members
            .memberList(v-for="i in member")
                .memberImg(:style="{'background-image': 'url(' + i.src + ')'}")
                .PageText.memberName {{i.name}}
    .outcomeMessage
        .PageTitle 留言區
        .Message(v-for="(i,id) in message")
            .messagePhoto(:style="{'background-image': 'url(' + photo + ')'}")
            .messageText
                .messageName {{name}}
                .messageDate {{time}}
                .messageContent {{message[id]}}
        input#messageInput(v-model="enteringMessage" @keyup.enter="addOn(enteringMessage)" placeholder="留言回應...")
    .tool
        .toolIcon#tool1(:style="{'background-image': 'url(' + tool1 + ')'}" @click="toMessage()")
        .toolIcon#tool2(:style="{'background-image': 'url(' + tool2 + ')'}" @click="changeLike()")
        .toolIcon#tool3(:style="{'background-image': 'url(' + tool3 + ')'}" @click="changeMark()")
        .toolIcon#tool4(:style="{'background-image': 'url(' + tool4 + ')'}")
            .visited {{visited}}
</template>
<script>
var likePressed=0;
var markPressed=0;
var messageId=0;
export default {
    created(){
        window.scroll({
            top: 0,
            left: 0,
            behavior: 'smooth'
        });
    },
    data(){
        return{
            member: [
                {src:"https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/濕地裝置藝術/member1.png",name:"侯即婷"},
                {src:"https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/濕地裝置藝術/member2.png",name:"陶陸仁"},
                {src:"https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/濕地裝置藝術/member3.png",name:"洪艾咪"},
                {src:"https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/濕地裝置藝術/member4.png",name:"夏端雲"},
            ],
            tool1:"https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/chat_bubble-24px.svg",
            tool2:"https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/favorite-24px.svg",
            tool3:"https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/bookmark-24px.svg",
            tool4:"https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/ic_visibility_24px.svg",
            visited:"104",
            message: [],
            enteringMessage:"",
            photo:"https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/濕地裝置藝術/member5.png",
            name:"白池",
            time:"2020/06/05 13:19",
            img: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/濕地裝置藝術/intro.png',
            title: '濕地裝置藝術',
            date: '2020/05/06 17:32',
            intro: '台灣約有47,627公頃的濕地，濕地的保育一直是台灣的重要環保議題， 政府也制定了法律如「濕地保育法」來保護濕地的使用。為了能夠表達 想要與濕地共生的理念，且受到濕地藝術季的啟發，我決定召集對這個 同樣有興趣的夥伴，一起在濕地建置一個裝置藝術，與這塊土地好好連 結。',
            processtext: '計畫初期，我們有畫出我們的草圖，也有到濕地實地探勘，接著我們進行了多次小組討論，但 還沒有真正執行計畫便被迫中止。',
            processpic1: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/濕地裝置藝術/process1.png',
            processpic2: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/濕地裝置藝術/process2.png',
            processpic3: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/濕地裝置藝術/process3.png',
            member1: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/濕地裝置藝術/member1.png',
            member2: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/濕地裝置藝術/member2.png',
            member3: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/濕地裝置藝術/member3.png',
            member4: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/濕地裝置藝術/member4.png',
            member5: 'https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/濕地裝置藝術/member5.png',
            st: '計畫中期，當我們的小組已經討論完了之後，卻發現我們的濕地使用申請被相關單位駁回，原 因爲我們的裝置既不符合使用規格，也會對濕地造成破壞。起出我們嘗試修改我們的裝置草圖， 卻發現我們的裝置無論怎麼改都無法滿足每個人的期待，且相關的申請也因此延後了，於是在 討論過後決定先中止計劃，等待整理過後再重新出發。',
            a: '我們在討論的時候，應該要分配組員同時與濕地保護協會的溝通。因為我們從來沒有執行計畫 的經驗，所以不知道初期規劃要做到什麼樣的程度，我們把重心都放在了設計還有環保理念上 卻忽略了小細節，非常可惜。',
            r: '幸運的是，團隊中有個家裡賣醫療器材的成員非常的富裕，家裡擁有一片私人沙灘，爺爺希望他 回來繼承渡假飯店的家業，所以我們將繼續完成我們的裝置藝術，重新包裝後放置在沙灘上，我 們希望以永續作為飯店的主軸，連帶改變飯店的裝修和設施。這次的失敗算是因禍得福，我們非 常感謝這個平台，把我們聚在一起，所以我們決定再發起「沙灘裝置藝術2.0」！我們希望招募 飯店管理、建築設計人才，謝謝大家！'

        }
    },
    methods: {
        addOn(t){     
            this.message.push(t);
            messageId++;
            console.log(messageId);
            this.enteringMessage="";
            // document.getElementById("messageInput").value = "";
        },
        changeLike(){
            if(likePressed==0){
                document.getElementById("tool2").style.backgroundImage="url(https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/favorite-24px-red.png)";
                document.getElementById("tool2").style.filter="contrast(100%)";
                likePressed=1;
            }
            else{
                document.getElementById("tool2").style.backgroundImage="url(https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/favorite-24px.svg)";
                document.getElementById("tool2").style.filter="contrast(10%)";
                likePressed=0;
            }
        },
        changeMark(){
            if(markPressed==0){
                document.getElementById("tool3").style.backgroundImage="url(https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/bookmark-24px-yellow.png)";
                document.getElementById("tool3").style.filter="contrast(100%)";
                markPressed=1;
            }
            else{
                document.getElementById("tool3").style.backgroundImage="url(https://raw.githubusercontent.com/jerry914/DooperationSite/master/assets/bookmark-24px.svg)";
                document.getElementById("tool3").style.filter="contrast(10%)";
                markPressed=0;
            }
        },
        toMessage(){
            window.scroll({
                top: 3500,
                left: 0,
                behavior: 'smooth'
            });
        }
    }
}
</script>
<style lang="sass">
